<template>
    <FloatingPanel :width="width" :height="height"  @close="$emit('close')">
        <div class="command">
            ចម្ងាយពីទន្លេ៖ 
            <input class="buffer-input" type="text" id="buffer-km" v-model="bufferSize" @keypress="isNumber($event)">
            គ.ម
            <input class="run-button" type="button" value="Run" @click="onRun">
            <input class="only-sand-checkbox" type="checkbox" v-model="onlySandProject" name="check" id="check1">
            <label for="check1">យកតែគម្រោងខ្សាច់សំណង់ និងល្បាយខ្សាច់</label>
        </div>


        <div class="list-title">{{title}}</div>

        <v-data-table 
            :headers="headers" 
            :items="projects" 
            :loading="isLoading"
            loading-text="Loading... Please wait"
            :items-per-page="5"
            height="400"
            />

    </FloatingPanel>
</template>

<script>
const t = require('@turf/turf');
import FloatingPanel from "@/components/FloatingPanel.vue";
import func from '@/scripts/helpers.js';
import events from "@degjs/event-aggregator";
import store from "../store";
import Table from '@/components/Table.vue';

export default {
    name: 'SandProjects',

    props:{
        width: {
            type: Number,
            default: 400
        },
        height: {
            type: Number,
            default: 200
        }
    },

    components: {
        FloatingPanel,
        Table
    },

    data() {
        const river = func.geojson.empty();
        const allProjects = store.getters.geojson.features;
        const projects = func.geojson.features2DataTable(allProjects).filter(p => p.case===1);
        return {
            river,
            isLoading: true,
            bufferSize: 0.5,
            onlySandProject: false,
            projects,
            headers:[
                { text: 'ម្ចាស់គម្រោង (KH)', value: 'owner_kh' },
                { text: 'ម្ចាស់គម្រោង (EN)', value: 'owner_en' },
                { text: 'ប្រភេទ', value: 'type' },
                { text: 'ខេត្ត', value: 'loc_khet' },
            ],
            
        }
    },
    
    async beforeCreate() {
        this.isLoading = true;
        const river = await func.fetch.riverAsync();
        this.river = river;
        this.$emit('data-river', river);
        const projects = func.geojson.create(this.projects.map(p => p.feature));
        this.$emit('data-projects', projects);
        this.isLoading = false;
    },

    computed:{
        title(){
            const onRiverOnly = this.bufferSize < 0.2;
            const projectCount = func.en2kh(this.projects.length);
            const withSandTitle = this.onlySandProject? 'ខ្សាច់​សំណង់ និង​ល្បាយ​ខ្សាច់ ': '';
            if(onRiverOnly){ return `គម្រោង​${withSandTitle} នៅលើ​ដងទន្លេ​មេគង្គ (${projectCount}គម្រោង)`; }

            const bufferSize = func.en2kh(this.bufferSize.toString());
            return `គម្រោង​​${withSandTitle}នៅលើ​ដងទន្លេ​មេគង្គ និងជុំវិញ ${bufferSize}គ.ម (${projectCount}គម្រោង)`;
        },
        isEmptyRiver(){
            return func.geojson.isEmpty(this.river);
        },
        bufferedZone(){
            return this.isEmptyRiver? this.river: func.geojson.buffer(this.river, this.bufferSize);
        },
        selectedProjects(){
            const projects = store.getters.geojson;
            const features = projects.features;
            console.log(features);
            const bufferedZone = this.bufferedZone;
            console.log(bufferedZone);
            const selected = features.filter(f => t.booleanIntersects(f, bufferedZone));
            console.log(selected);
        }

    },

    methods:{
        isIntersect(testFeature, boundFeature) {
            
            const overlap = t.booleanIntersects(testFeature, boundFeature);
            return overlap;
        },
        isNumber: function(evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        onRun(){

            // TODO: promisify
            // create projects in mekong
            this.isLoading = true;
            this.projects = [];
            const projects = store.getters.geojson;
            const features = projects.features;
            const bufferedZone = this.bufferedZone;
            this.$emit('data-river', bufferedZone);
            const selected = features.filter(f => t.booleanIntersects(f, bufferedZone));
            this.projects = func.geojson.features2DataTable(selected);
            const geo = func.geojson.create(selected);
            console.log('selected: ',bufferedZone);
            console.log('projects-talbe: ',this.projects);
            console.log('geo: ',geo);

            this.$emit('data-projects', geo);
            this.isLoading = false;
            console.log(selected);
            // console.log('selected: ', this.selectedProjects);
            // const selected = features.filter(f => t.booleanOverlap(f, this.bufferedZone));
            // console.log(selected);
        }
    }


}
</script>

<style scoped>
.loading {
    text-align: center;
    margin: 9em;
}

.command {
    margin: 5px 10px;
}

.buffer-input {
    width: 50px;
    height: 23px;
    text-align: center;
    border: 1px solid gray;
}

.run-button {
    width: 50px;
    height: 23px;
    text-align: center;
    background-color: green;
    color: aliceblue;
    border-radius: 3px;
    border: 1px solid aliceblue;
    margin: 0px 10px;
}
.only-sand-checkbox {
    margin: 0px 5px;
}

.list-title {
    font-size: 14px ;
    font-family: 'Moul', Arial, Helvetica, sans-serif, cursive;
    text-align: center;
    background-color: white;
    padding: 10px;
}
</style>